@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;0,600;0,700;1,400;1,600;1,700");

.snapshot-uninstall-prompt {
    h3 {
        margin: 0;
        padding: 0;
        font-family: 'Open Sans', sans-serif;
        font-size: 16px;
        font-weight: bold;
        line-height: 30px;
        letter-spacing: -0.58px;
        color: #333333;
    }

    p {
        font-family: 'Open Sans', sans-serif;
        color: #23282D;
        font-size: 13px;
        letter-spacing: -0.25px;
        line-height: 22px;
    }

    a {
        font-weight: 600;
        color: #1E8CBE;
        text-decoration: none;
    }

    .snapshot-v3-uninstall-hero {
        position: absolute;
        width: 100px;
        height: 125px;
        background-color: #FFEDED;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url(../img/notice-background.svg);

        &.snapshot-hidden-branding {
            display: none;
        }
    }

    .snapshot-v3-uninstall-text {
        padding-left: 120px;
        padding-right: 30px;

        &.snapshot-hidden-branding {
            padding-left: 15px;
        }
	}
}

.snapshot3-uninstall-confirmation-modal {
    .ui-dialog-titlebar {
        background: #F9F9F9;
        border-bottom: none;
        height: 40px;
        cursor: default;
    }

    .ui-dialog-titlebar-close {
        width: 40px;
        height: 40px;
        color: #888888;
        margin-right: 5px;

        &:hover {
            color: #888888;
        }

        &:focus {
            box-shadow: none;
            color: #888888;
        }
    }

    .ui-dialog-content {
        overflow: initial;
        margin: 42px 40px 0px;
        padding: 0;

        h3 {
            font-family: 'Open Sans', sans-serif;
            font-size: 16px;
            font-weight: 700;
            color: #333333;
            letter-spacing: -0.31px;
            line-height: 22px;
        }

        p {
            font-family: 'Open Sans', sans-serif;
            font-size: 13px;
            font-weight: 400;
            color: #333333;
            letter-spacing: -0.25px;
            line-height: 22px;
        }
    }

    .ui-dialog-buttonpane {

        padding: 0px 16px 40px 16px;
        border-top: none;
        background: #FFFFFF;

        .ui-dialog-buttonset {
            float: none;

            .snapshot-button {

                &.snapshot-button-cancel {
                    background: #F7F7F7;
                    border-color: #CCCCCC;
                    &:focus {
                        border-color: #CCCCCC;
                        box-shadow: 0 0 0 1px #CCCCCC;
                    }
                    color: #555555;
                    margin-left: 24px;
                    width: 77px;
                }

                &.snapshot-button-uninstall {
                    background: #E35654;
                    border-color: #E35654;
                    &:focus {
                        border-color: #E35654;
                        box-shadow: 0 0 0 1px #E35654;
                    }
                    color: #FFFFFF;
                    width: 101px;
                    margin-left: 10px;
                }
            }
        }

    }

}